"use client";
import React, { useState, useEffect } from "react";
import { Star } from "lucide-react";

const initialTasks = [
  {
    id: 1,
    title: "Collect 10 plastic bottles",
    description: "Find and collect 10 bottles from your area.",
    difficulty: "Easy",
    points: 10,
    images: [],
    startedAt: null,
    lastCompleted: null,
  },
  {
    id: 2,
    title: "Clean a park bench",
    description: "Wipe down and clean a park bench thoroughly.",
    difficulty: "Medium",
    points: 25,
    images: [],
    startedAt: null,
    lastCompleted: null,
  },
  {
    id: 3,
    title: "Pick up 15 pieces of trash",
    description: "Collect 15 random pieces of trash.",
    difficulty: "Hard",
    points: 50,
    images: [],
    startedAt: null,
    lastCompleted: null,
  },
];

export default function TasksPage() {
  const [tasks, setTasks] = useState(initialTasks);
  const [activeTaskId, setActiveTaskId] = useState(null);

  const startTask = (taskId) => setActiveTaskId(taskId);
  const finishTask = (taskId) => {
    setTasks((prev) =>
      prev.map((t) =>
        t.id === taskId
          ? { ...t, lastCompleted: Date.now(), startedAt: null }
          : t
      )
    );
    setActiveTaskId(null);
  };

  const uploadImage = (taskId, file) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      setTasks((prev) =>
        prev.map((t) =>
          t.id === taskId ? { ...t, images: [...t.images, reader.result] } : t
        )
      );
    };
    reader.readAsDataURL(file);
  };

  const difficultyColors = {
    Easy: "bg-green-400 shadow-[0_0_6px_#00ff91]",
    Medium: "bg-yellow-400 shadow-[0_0_6px_#ffee00]",
    Hard: "bg-red-400 shadow-[0_0_6px_#ff3300]",
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-[#e0fdf9] via-[#f0fff9] to-[#d3fff7] p-8">
      <h1 className="text-4xl md:text-5xl font-extrabold text-center mb-12 text-green-600 drop-shadow-[0_0_10px_#00ff91]">
        Daily CleanTM Tasks
      </h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-10 max-w-7xl mx-auto">
        {tasks.map((task) => {
          const isActive = activeTaskId === task.id;

          return (
            <div
              key={task.id}
              className="
                relative rounded-3xl p-6 bg-white/20 backdrop-blur-md
                border border-green-400/30 shadow-[0_0_20px_5px_rgba(0,255,145,0.05)]
                hover:shadow-[0_0_35px_15px_rgba(0,255,145,0.15)]
                transition-all duration-500 flex flex-col justify-between
                overflow-hidden
              "
            >
              {/* Task Header */}
              <div className="flex justify-between items-center mb-3">
                <h2 className="text-2xl font-bold text-green-500 drop-shadow-[0_0_6px_#00ff91]">
                  {task.title}
                </h2>
                <div
                  className={`flex items-center gap-1 px-2 py-1 rounded-full text-white text-sm ${
                    difficultyColors[task.difficulty]
                  }`}
                >
                  <Star className="w-4 h-4" /> {task.difficulty}
                </div>
              </div>

              {/* Description */}
              <p className="text-gray-800 mb-2">{task.description}</p>
              <p className="text-green-400 font-semibold mb-3">
                Points: {task.points}
              </p>

              {/* Images */}
              {task.images.length > 0 && (
                <div className="flex flex-wrap gap-2 mb-3">
                  {task.images.map((img, i) => (
                    <img
                      key={i}
                      src={img}
                      className="h-20 w-20 rounded-xl border border-green-400 object-cover"
                    />
                  ))}
                </div>
              )}

              {/* Buttons */}
              <div className="flex flex-col gap-2 mt-4">
                {!isActive && (
                  <button
                    onClick={() => startTask(task.id)}
                    className="w-full py-2.5 rounded-full bg-green-400 hover:bg-green-500 font-bold transition shadow-md hover:shadow-[0_0_20px_#00ff91]"
                  >
                    Start Task
                  </button>
                )}
                {isActive && (
                  <button
                    onClick={() => finishTask(task.id)}
                    className="w-full py-2.5 rounded-full bg-green-400 hover:bg-green-500 font-bold transition shadow-md hover:shadow-[0_0_20px_#00ff91]"
                  >
                    Finish Task
                  </button>
                )}
              </div>

              {/* File Upload */}
              {isActive && (
                <input
                  type="file"
                  onChange={(e) => uploadImage(task.id, e.target.files[0])}
                  className="mt-3 block w-full text-sm text-green-700 file:bg-green-500 file:text-white file:px-4 file:py-2 file:rounded-full cursor-pointer"
                />
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
}
